.show-positive-image{
  background-image: url('./water-bottle-image.jpeg');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  height: 80vh;;
}
  .isTrashable {
    display: flex;
    justify-content: center;
    margin: 20px 0;
  }
  
  .isTrashable button {
    background-color: #4CAF50;
    border: none;
    color: white;
    padding: 10px 20px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    cursor: pointer;
  }
  
  .impact-container {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    margin: 20px 0;
    padding-left: 7rem;
    padding-right: 7rem;
    display: none;

      
  }
  
  .impact-container img {
    max-width: 100%;
    height: auto;
    
  }
  
  .text-container {
    padding: 10px;
    background-color: white;
  }
  .video-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 500px;
    position: relative;
  }
  
  .video-container webcam {
    width: 600px;
    height: 100%;
    z-index: 1;
  }
  
  .video-container canvas {
    position: absolute;
    top: 0;
    left: 400px;
    width: 600px;
    height: 500px;
    z-index: 2;
  }
  
  .impact-container {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    margin: 20px 0;
    padding-left: 7rem;
    padding-right: 7rem;
    opacity: 0;
    height: 0;
    overflow: hidden;
    transition: height 0.5s ease-out, opacity 0.5s ease-out;
  }
  
  .impact-container.show {
    opacity: 1;
    height: auto;
  }
  
  .impact-container.hide {
    height: 0;
  }

  .welcome-message {
    font-size: 36px;
    font-weight: bold;
    text-align: center;
    color: #4caf50;
    margin-top: 50px;
    margin-bottom: 30px;
  }

  .header-container{
    background-image: url('./recycling-cans.jpeg');
    background-position: center;
  }
  
  
  